import React from "react";
import SubjectAllStudent from "./SubjectAllStudent";
import SubjectBatchAllStudent from "./SubjectBatchAllStudent";

function SubjectCatalog({ cid, bid, isDepartment }) {
  return (
    <>
      {bid ? (
        <SubjectBatchAllStudent bid={bid} isDepartment={isDepartment} />
      ) : (
        <SubjectAllStudent cid={cid} isDepartment={isDepartment} />
      )}
    </>
  );
}

export default SubjectCatalog;
